import { Fragment, memo, useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import {
  updateUser,
  clearErrors,
  getUserDetails,
} from "../../../../redux/actions/userAction";
import { IoMdPerson } from "react-icons/io";
import { FiMail } from "react-icons/fi";
import { UPDATE_USER_RESET } from "../../../../redux/constants/userConstant";
import { MdAccountTree } from "react-icons/md";
import H1 from "../../../ReusableComponent/Heading/H1/H1";
import Input from "../../../ReusableComponent/Inputs/Input";
import Loader from "../../../layout/Loader/Loader";

const EditUser = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();
  const { id } = useParams();

  const { isUpdated, error, loading } = useSelector(
    (state) => state.updateUser
  );
  const { isAuthenticated, user: authUser } = useSelector(
    (state) => state.user
  );

  const { user: getUser, loading: getUserLoader } = useSelector(
    (state) => state.getUser
  );

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [role, setRole] = useState("");

  const updateUserSubmit = (e) => {
    e.preventDefault();
    const myForm = new FormData();
    myForm.set("name", name);
    myForm.set("email", email);
    myForm.set("role", role);
    dispatch(updateUser(id, myForm));
  };

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    if (authUser) adminAuth(true, authUser, navigate, alert);

    if (getUser) {
      if (getUser && getUser._id !== id) {
        dispatch(getUserDetails(id));
      } else {
        setName(getUser.name);
        setEmail(getUser.email);
        setRole(getUser.role);
      }
    } else {
      dispatch(getUserDetails(id));
    }

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
      alert.success("User Updated Successfully");
      dispatch({ type: UPDATE_USER_RESET });
      navigate("/admin/users");
    }
  }, [
    isAuthenticated,
    authUser,
    navigate,
    alert,
    dispatch,
    error,
    isUpdated,
    getUser,
    id,
  ]);

  return (
    <Fragment>
      <MetaData title={`Update Product`} />

      <div className="dashboard">
        <SideBar />

        <div className="newProductContainer">
          {getUserLoader ? (
            <Loader />
          ) : (
            <form className="form" onSubmit={updateUserSubmit}>
              <H1>Edit User Role</H1>
              <Input
                icon={<IoMdPerson />}
                type="text"
                placeholder="Name"
                name="name"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />

              <Input
                icon={<FiMail />}
                type="email"
                placeholder="Email"
                name="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
              <Input
                type="select"
                icon={<MdAccountTree />}
                name="role"
                onChange={(e) => setRole(e.target.value)}
              >
                <option value="">Select Category</option>
                <option value="admin">admin</option>
                <option value="user">user</option>
              </Input>

              <Input
                type="submit"
                value="Update"
                disabled={loading ? true : false || role === "" ? true : false}
              />
            </form>
          )}
        </div>
      </div>
    </Fragment>
  );
};

export default memo(EditUser);
